.support {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 130px;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    width: 400px;
    height: 550px;
    box-shadow: 0px 0px 40px 10px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}

.support-title {
    margin: 15px 0 25px;
    font-size: 28px;
}

.support-username {
    width: 350px;
    height: 40px;
    margin-bottom: 24px;
    padding-left: 7px;
    font-size: 20px;
    border: 1px solid black;
    outline: none;
}

.support-surname {
    width: 350px;
    height: 40px;
    margin-bottom: 24px;
    padding-left: 7px;
    font-size: 20px;
    border: 1px solid black;
    outline: none;
}

.support-email {
    width: 350px;
    height: 40px;
    margin-bottom: 24px;
    font-size: 20px;
    padding-left: 7px;
    border: 1px solid black;
    outline: none;
}

.support-textarea {
    width: 350px;
    height: 200px;
    font-size: 20px;
    padding-left: 7px;
    padding-top: 5px;
    outline: none;
    border: 1px solid black;
    resize: none;
}

.support-submit {
    margin-top: 20px;
    width: 120px;
    height: 40px;
    font-size: 18px;
    background: black;
    color: white;
    border: none;
}

.support-username__error {
    position: absolute;
    top: 51px;
    left: 24px;
    font-size: 17px;
    color: red;
}

.support-usersurname__error {
    position: absolute;
    top: 115px;
    left: 24px;
    font-size: 17px;
    color: red;
}

.support-useremail__error {
    position: absolute;
    top: 179px;
    left: 24px;
    font-size: 17px;
    color: red;
}

.support-textarea__error {
    position: absolute;
    top: 244px;
    left: 24px;
    color: red;
}

@media screen and (max-width: 768px) {

    .support {
        display: flex;
        flex-direction: column;
        position: relative;
        top: 130px;
        margin-left: auto;
        margin-right: auto;
        padding-bottom: 20px;
        align-items: center;
        width: 310px;
        height: 550px;
        box-shadow: 0px 0px 40px 10px rgba(0, 0, 0, 0.25);
        border-radius: 10px;
    }
    
    .support-title {
        margin: 8px 0 20px;
        font-size: 23px;
    }
    
    .support-username {
        width: 270px;
        height: 37px;
        margin-bottom: 24px;
        padding-left: 7px;
        font-size: 20px;
        border: 1px solid black;
        outline: none;
    }
    
    .support-surname {
        width: 270px;
        height: 37px;
        margin-bottom: 24px;
        padding-left: 7px;
        font-size: 20px;
        border: 1px solid black;
        outline: none;
    }
    
    .support-email {
        width: 270px;
        height: 37px;
        margin-bottom: 24px;
        font-size: 20px;
        padding-left: 7px;
        border: 1px solid black;
        outline: none;
    }
    
    .support-textarea {
        width: 270px;
        height: 190px;
        font-size: 20px;
        padding-left: 7px;
        padding-top: 5px;
        outline: none;
        border: 1px solid black;
        resize: none;
    }
    
    .support-submit {
        margin-top: 20px;
        width: 120px;
        height: 40px;
        font-size: 18px;
        background: black;
        color: white;
        border: none;
    }
    
    .support-username__error {
        position: absolute;
        top: 34px;
        left: 20px;
        font-size: 17px;
        color: red;
    }
    
    .support-usersurname__error {
        position: absolute;
        top: 96px;
        left: 20px;
        font-size: 17px;
        color: red;
    }
    
    .support-useremail__error {
        position: absolute;
        top: 157px;
        left: 20px;
        font-size: 17px;
        color: red;
    }
    
    .support-textarea__error {
        position: absolute;
        top: 219px;
        left: 20px;
        color: red;
    }
}