.card {
    display: flex;
    width: 1000px;
    margin-top: 10px;
    margin-right: auto;
    margin-left: auto;
    flex-direction: column;
    border: 2px solid #e7e4e4;
    border-radius: 15px;
    padding: 15px;
}

.card__title {
    font-size: 20px;
    padding-bottom: 10px;
    padding-left: 5px;
}

.card__description {
    font-size: 18px;
    padding-top: 10px;
    padding-left: 5px;
    line-height: 1.5;
}

.card__price {
    font-size: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5px;
}

.card img {
    width: 80px;
    height: 80px;
    padding-left: 5px;
}

.card__opening {
    font-size: 18px;
    padding-left: 5px;
}

.card__opening a {
    text-decoration: none;
    padding-bottom: 3px;
    border-bottom: 1px solid black;
    color: black;
}

.card__opening a:hover {
    border-bottom: 1px solid rgba(0,0,0,0);
}

