.header {
    display: flex;
    width: 100%;
    padding: 0 40px;
    justify-content: space-between;
    height: 100px;
    box-shadow: 0 3px 3px #0082c6;
    align-items: center;
    background: #e7e4e4;
    position: fixed;
    z-index: 1;
}
    
.header-heading {
    display: flex;
    align-items: center;
}
    
h1 {
    font-size: 40px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    margin-left: 25px;
    color: black;
}
    
.header-logo {
    width: 110px;
    height: 70px;
}
    
.header-title {
    display: flex;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 25px;
    color: black;
}
    
.header-title__feedback {
    padding-left: 30px;
}
    
.header-title__basket {
    padding-left: 30px;
}
    
.header-title__basket img {
    width: 25px;
    height: 25px;
}
    
.header-title__main a {
    text-decoration: none;
    color: black;
}
    
.header-title__feedback a {
    text-decoration: none;
    color: black;
}

@media screen and (max-width: 768px) {

    .header {
        display: flex;
        width: 100%;
        flex-direction: column;
        justify-content: center;
        height: 100px;
        box-shadow: 0 3px 3px #0082c6;
        background: #e7e4e4;
        position: fixed;
        z-index: 1;
    }
    
    h1 {
        margin: 0;
        font-size: 30px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        color: black;
    }
    
    .header-logo {
        width: 110px;
        height: 70px;
        display: none;
    }
    
    .header-title {
        display: flex;
        padding-top: 15px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        font-size: 18px;
        color: black;
    }
    
    .header-title__feedback {
        padding-left: 20px;
    }
    
    .header-title__basket {
        padding-left: 20px;
    }
    
    .header-title__basket img {
        width: 25px;
        height: 25px;
    }
    
    .header-title__main a {
        text-decoration: none;
        color: black;
    }
    
    .header-title__feedback a {
        text-decoration: none;
        color: black;
    }
}